.profile__title {
  max-width: 285px;
  margin: 0 18px 7px 0;
  font-family: Inter, Arial, sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 42px;
  line-height: 48px;
  color: #fff;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

@media screen and (max-width: 615px) {
  .profile__title {
    margin: 0 7px 9px 0;
    font-size: 27px;
    line-height: 33px;
  }
}
